@import url(https:/fonts.googleapis.com/css2?family=Lato&display=swap);
@import url(https://fonts.googleapis.com/css?family=Open+Sans:400,600&display=swap);
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'Lato', sans-serif;
}

.general, .bioinformatics, .cellular, .biomechanics, .sysandsig {
  display: flex;
  height: 90vh;
  align-items: center;
  justify-content: center;
  font-size: 3rem;
}
.navbar {
    background-color: #121c57;
    height: 80px;
    display: flex;
    justify-content: start;
    align-items: center;
}

.menu-bars {
    margin-left: 2rem;
    font-size: 2rem;
    background: none;
}

.nav-menu {
    background-color: #0a1033;
    width: 250px;
    height: 100vh;
    display: flex;
    justify-content: center;
    position: fixed;
    top: 0;
    left: -100%;
    transition: 1050ms;
}

.nav-menu.active {
    left: 0;
    transition: 550ms;
}

.nav-text {
    display: flex;
    justify-content: start;
    align-items: center;
    padding: 8px 0px 8px 16px;
    list-style: none;
    height: 60px;
    transition: transform .2s;
}

.nav-text:hover {
    transform: scale(1.08);
}

.nav-text a {
    text-decoration: none;
    color: #f5f5f5;
    font-size: 18px;
    width: 95%;
    height: 100%;
    display: flex;
    align-items: center;
    padding: 0 16px;
    border-radius: 4px;
}

.nav-text a:hover {
    background-color: #1a83ff;
}

.nav-menu-items {
    width: 100%;
}

.navbar-toggle {
    background-color: #121c57;
    width: 100%;
    height: 80px;
    display: flex;
    justify-content: start;
    align-items: center;
}

span {
    margin-left: 16px;
}

.header {
    position: absolute;
    left: 33%;
    padding: 60px;
    text-align: center;
    float: right;
    color: white;
    font-size: 30px;
}
/* Style the accordion section */
.accordion__section {
  display: flex;
  flex-direction: column;
}

/* Style the buttons that are used to open and close the accordion panel */
.accordion {
  background-color: #eee;
  color: #444;
  width: 100%;
  cursor: pointer;
  padding: 18px;
  display: flex;
  align-items: center;
  border: none;
  outline: none;
  transition: background-color 0.6s ease;
}

/* Add a background color to the button if it is clicked on (add the .active class with JS), and when you move the mouse over it (hover) */
.accordion:hover {
  background-color: rgb(167, 155, 155);
}

/* Style the accordion content title */
.accordion__title {
  font-family: "Open Sans", sans-serif;
   font-weight: 600;
  font-size: 24px;
  text-align: center;
}

/* Style the accordion content panel. Note: hidden by default */
.accordion__content {
  background-color: white;
  overflow: hidden;
  transition: max-height 0.6s ease;
}


/* Style the accordion content text */
.accordion__text {
  font-family: "Open Sans", sans-serif;
  font-weight: 400;
  font-size: 18px;
  padding: 18px;
}

.row:after {
  content: "";
  display: table;
  clear: both;
}

.accordion__section .closebtn {
  color: #AA9;
  font-weight: bold;
  float: right;
  cursor: pointer;
  font-size: 1.3em;
  justify-content: 'space-between';
}

.close {
  float:right;
}

.closebtn:hover {
  color: #AC2500;
}


.formWrapper{
  margin: auto;
}

.noteButton{
  width: 10%;
  border: none;
  margin: 0px;
  border-top-right-radius: 4px;
  border-bottom-right-radius: 4px;
  padding: 8px 24px;
  font-size: 1.3em;
  font-family: "Open Sans", sans-serif;
  background-color: #121c57;
  color: #fff;
  transition: 0.2s ease-out;
}

.noteButton:hover{
  background-color: #3043b4;
  color: #fff;
  transition: 0.2s ease-out;
}

.noteInput{
  width: 90%;
  height: 43px;
  align-self: center;
  min-width: 800px;
  font-size: 1.3em;
  padding-left: 20px;
  border-top-left-radius: 4px;
  border-bottom-left-radius: 4px;
  font-family: 'Droid Sans', sans-serif;
  background-color: rgba(230, 230, 230, 0.8);
  color: #fff;
  transition: 0.2s ease-out;
}

.noteInput:focus{
  background-color: #ffffff;
  color: #111;
  transition: 0.2s ease-in;
}

.noteInput:hover{
  background-color: #ffffff;
  color: #111;
  transition: 0.2s ease-in;
}
.row:after {
    content: "";
    display: table;
    clear: both;
  }

  .column {
      float: left;
      padding: 10px;
  }

  .left {
      width: 50%;
  }

  .middle {
      width: 20%;
  }

  .right {
      width: 25%;
  }

  .delete {
      width: 5%;
      float: right;
      padding: 10px;
  }

  .deletebtn:hover {
    color: rgb(216, 65, 65);
  }

.formWrapper{
    margin: auto;
  }
  
  .submitTask{
    width: 10%;
    border: none;
    margin: 0px;
    border-top-right-radius: 4px;
    border-bottom-right-radius: 4px;
    padding: 4px 18px;
    font-size: 1em;
    font-family: "Open Sans", sans-serif;
    background-color: #121c57;
    color: #fff;
    transition: 0.2s ease-out;
  }
  
  .submitTask:hover{
    background-color: #3043b4;
    color: #fff;
    transition: 0.2s ease-out;
  }
  
  .content, .date, .additional {
    width: 20%;
    height: 25px;
    align-self: center;
    min-width: 300px;
    font-size: 1.1em;
    padding-left: 20px;
    border-top-left-radius: 4px;
    border-bottom-left-radius: 4px;
    font-family: 'Droid Sans', sans-serif;
    background-color: rgba(236, 234, 234, 0.8);
    color: rgb(0, 0, 0);
    transition: 0.2s ease-out;
  }
  
  .content:focus, .date:focus, .additional:focus {
    background-color: #ffffff;
    color: #111;
    transition: 0.2s ease-in;
  }
  
  .content:hover, .date:hover, .additional:hover {
    background-color: #ffffff;
    color: #111;
    transition: 0.2s ease-in;
  }
